import { Component, h, Prop, Host, FunctionalComponent } from '@stencil/core';
import AccountIcon from '@material-design-icons/svg/outlined/account_circle.svg';

@Component({
  tag: 'rwc-post-card',
  styleUrl: 'post-card.scss',
  shadow: true
})
export class RwcPostCard {
  /**
   * Path to the avatar.
   */
  @Prop({ reflect: true }) avatar: string = '';

  /**
   * Set avatar rounded.
   */
  @Prop() roundedAvatar: boolean = false;

  /**
   * Set rendering of box-shadow.
   */
  @Prop({ reflect: true }) renderShadow: boolean = true;

  /**
   * Set rendering of border.
   */
  @Prop({ reflect: true }) renderBorder: boolean = false;

  /**
   * Animate border on hover.
   */
  @Prop({ reflect: true }) animateBorder: boolean = false;

  /**
   * Change cursor to pointer on hover.
   */
  @Prop() pointer: boolean = false;

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <rwc-card
          class="card"
          renderShadow={ this.renderShadow }
          renderBorder={ this.renderBorder }
          animateBorder={ this.animateBorder }
          pointer={ this.pointer }
        >
          <div class="container">
            <div class="user">
              <div class="avatar">
                <slot name="avatar">
                  { this.renderAvatar() }
                </slot>
              </div>
              <div class="name">
                <slot name="name"></slot>
              </div>
            </div>
            <div class="text">
              <div class="heading content-spacing">
                <slot name="heading"></slot>
              </div>
              <div class="description content-spacing">
                <slot name="description"></slot>
              </div>
              <div class="footer content-spacing">
                <slot name="footer"></slot>
              </div>
            </div>
          </div>
        </rwc-card>
      </Host>
    )
  }

  /**
   * Renders the avatar element.
   */
  renderAvatar(): FunctionalComponent {
    if (this.avatar) {
      return (
        <img
          src={ this.avatar }
          alt="avatar"
          class={ {
            icon: true,
            rounded: this.roundedAvatar
          } }
        />
      )
    }
    return (
      <div innerHTML={ AccountIcon } class="svg-icon"></div>
    )
  }
}
