@import 'global.scss';

:host {
  /**
  * @prop --post-card-border-width: The border width of the post card.
  * @prop --post-card-border-style: The style of the border of the post card.
  * @prop --post-card-border-color: The color of the border of the post card.
  * @prop --post-card-hover-border-color: The color of the border of the card on hover.
  * @prop --post-card-border-radius: The border radius of the post card.
  * @prop --post-card-border-transition: The transition of the border of the post card.
  * @prop --post-card-box-shadow: The shadow of the post card.
  * @prop --post-card-box-shadow-animated-hover: The animation of the box shadow of the post card on hover.
  * @prop --post-card-container-transition: The transition of the container of the post card.
  * @prop --post-card-inner-padding: The inner padding of the post card.
  * @prop --post-card-icon-size: The size of the icon of the post card.
  * @prop --post-card-icon-margin-right: The margin size from right of the icon of the post card.
  * @prop --post-card-icon-color: The color of the icon of the post card.
  * @prop --post-card-content-spacing: The bottom spacing of the content of the post card. 
  * @prop --post-card-content-justify-start: The horizontal aligment of the contents of the post card.
  * @prop --post-card-content-justify-end: The horizontal aligment of the contents of the post card.
  * @prop --post-card-font-size-lg: The large font size of the post cards.
  * @prop --post-card-font-size-md: The medium font size of the post card.
  * @prop --post-card-font-size-sm: The small font size of the post card.
  * @prop --post-card-gap: The gap between the elements of the post card.
  * @prop display: The display mode of the host element.
  * @prop font-family: The font family of the host element.
  */
  --post-card-border-width: 2px;
  --post-card-border-style: solid;
  --post-card-border-color: #{$rwc-color-border-primary};
  --post-card-hover-border-color: #{$rwc-color-border-active};
  --post-card-border-radius: #{$rwc-spacing--sm};
  --post-card-border-transition:
    border-bottom-color 0.4s,
    border-left-color 0.4s,
    border-right-color 0.4s,
    border-top-color 0.4s,
    width 0.4s ease-in-out,
    height 0.4s ease-in-out;
  --post-card-box-shadow: #{$rwc-box-shadow-lg};
  --post-card-box-shadow-animated-hover: #{$rwc-box-shadow-xl};
  --post-card-container-transition: box-shadow 0.4s ease-in-out;

  --post-card-content-justify-start: start;
  --post-card-content-justify-end: end;

  --post-card-content-spacing: #{$rwc-spacing--xs};
  --post-card-inner-padding: #{$rwc-spacing--md};

  --post-card-font-size-lg: #{$rwc-font-size-body-lg};
  --post-card-font-size-md: #{$rwc-font-size-body-md};
  --post-card-font-size-sm: #{$rwc-font-size-body-sm};

  --post-card-icon-size: 2.5rem;
  --post-card-icon-margin-right: 1rem;
  --post-card-icon-color: #{$rwc-color-grey-600};

  --post-card-gap: #{$rwc-spacing--xs};

  font-family: #{$rwc-font-family-default};

  display: block;
}

.card {
  --card-border-width: var(--post-card-border-width);
  --card-border-style: var(--post-card-border-style);
  --card-border-color: var(--post-card-border-color);
  --card-hover-border-color: var(--post-card-hover-border-color);
  --card-border-radius: var(--post-card-border-radius);
  --card-border-transition: var(--post-card-border-transition);
  --card-box-shadow-animated-hover: var(--post-card-box-shadow-animated-hover);
  --card-container-transition: var(--post-card-container-transition);
  --card-inner-padding: var(--post-card-inner-padding);
  --card-box-shadow: var(--post-card-box-shadow);
}

.pointer {
  cursor: pointer;
}

.user {
  display: flex;
  align-items: center;
  justify-content: var(--post-card-content-justify-start);
}

.name {
  font-size: var(--post-card-font-size-lg);
}

.heading {
  font-weight: bold;
  font-size: var(--post-card-font-size-md);
}

.description {
  font-size: var(--post-card-font-size-sm);
}

.footer {
  font-size: var(--post-card-font-size-sm);
  display: flex;
  align-items: center;
  justify-content: var(--post-card-content-justify-end);
  gap: var(--post-card-gap);
}

.content-spacing {
  margin-top: var(--post-card-content-spacing);
}

.avatar {
  align-self: center;
  flex-shrink: 0;
  height: var(--post-card-icon-size);
  width: var(--post-card-icon-size);
  margin-right: var(--post-card-icon-margin-right);

  svg {
    height: 100%;
    width: 100%;
  }

  path {
    fill: var(--post-card-icon-color);
  }

  .icon {
    width: 100%;
    height: 100%;
  }

  .rounded {
    border-radius: 50%;
  }
}
